.btn {
	//min-height: 43px;
	width: 256px;
	max-width: 100%;
	text-align: center;

	display: flex;

	justify-content: center;

	align-items: center;
	border: 1px solid #25378d;

	box-sizing: border-box;
	border-radius: 200px;
	font-family: 'Montserrat';
	font-style: normal;
	font-weight: 500;
	font-size: 13px;
	line-height: 1.2;
	padding: 14px 15px;
	display: flex;
	align-items: center;
	text-align: center;
	text-transform: uppercase;
	color: #25378d;
	transition-duration: .2s;
	cursor: pointer;

	&:hover {
		border-color: #AFB4BF;
		color: #AFB4BF;
	}

	&:active {
		border-color: #DEE6E9;
		color: #DEE6E9;
	}


	&--square {
		border-radius: 5px;
	}
}

.section-home-slider {}

.home-slider {
	position: relative;
	z-index: 1;
	margin: 0 auto;
	max-width: 100%;



	.slick-dots {
		position: absolute;

		z-index: 4;
		left: 95px;
		top: 50%;
		transform: translate(0, -50%);
		margin-top: 4px;

		display: flex;
		flex-direction: column;
		align-items: center;
		list-style: none;

		li {
			margin: 11px 0;
			list-style: none;

			width: 9px;
			height: 9px;
			border: 1px solid #FFFFFF;
			box-sizing: border-box;
			border-radius: 50%;
			transition: .3s;
			position: relative;

			&::before {
				content: '';
				display: block;
				width: 100%;
				height: 100%;
				background: #ffffff;
				border-radius: 50%;
				position: absolute;
				left: 0;
				top: 0;
				opacity: 0;
				transition: .3s;

			}

			&.slick-active {
				&::before {
					opacity: 1;
				}
			}

			button {
				display: none;
			}
		}
	}
}

.home-slider-slide {
	position: relative;
	height: calc(100vh - 90px);

	&::before {
		z-index: 3;
		display: block;
		width: 533px;
		height: 100%;
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		background: linear-gradient(90deg, rgba(3, 17, 83, 0.67) 0%, rgba(28, 40, 94, 0) 100%);
	}
}

.hms-pic {
