}


.read-more {
	cursor: pointer;
	display: inline-flex;
	font-size: 17px;
	color: #25378D;

	font-family: 'Montserrat';
	font-style: normal;
	font-weight: normal;
	position: relative;
	padding-right: 10px;
	transition-duration: .2s;

	&::after {
		transition-duration: .2s;
		position: absolute;
		top: 50%;
		right: 0;
		transform: translate(0, -50%);
		content: '';
		display: block;
		background: url(../img/arrow-more.svg) no-repeat center center;
		background-size: contain;
		width: 6px;
		height: 12px;
		margin-top: 2px;
	}

	&:hover {
		&::after {

			right: -5px;
		}

		text-decoration: underline;
	}

}


